<template>
    <section>
        <div class="border">
            <div class="theme-primary partition p-actions-expand d-flex" style="overflow: visible;">
                <div class="action-headings col-md-7">
                    <span id="spnreporttext" class="p-name text-white">{{$t('AddMeter')}}</span>
                </div>
            </div>
            <ValidationObserver v-slot="{ handleSubmit }">
                <form v-on:submit.prevent="handleSubmit(submit)" ref="form">
                    <div class="row p-3">
                        <div class="col-lg-12 mb-3">
                            <div class="heading-border mt-2">
                                <h5 class="h5"><span>{{$t('meteridentification')}}</span></h5>
                            </div>
                        </div>

                        <div class="col-lg-3 col-xl-3">
                            <div class="form-group">
                                <ValidationProvider rules="required" v-slot="{ errors }">
                                    <label>{{ $t('MeterName') }}:<span class="text-danger">*</span></label>
                                    <input v-model="Data.meterName" type="text"
                                        :class="errors[0] ? 'form-control is-invalid' : 'form-control'"
                                        name="meterName">
                                    <span class="invalid-feedback">{{ errors[0] }}</span>
                                </ValidationProvider>
                            </div>

                        </div>

                        <div class="col-lg-3 col-xl-3">
                            <div class="form-group">
                                <ValidationProvider rules="required" v-slot="{ errors }">
                                    <label>{{$t('Model')}}:<span class="text-danger">*</span></label>
                                    <input v-model="Data.meterModel" type="text"
                                        :class="errors[0] ? 'form-control is-invalid' : 'form-control'" name="Model">
                                    <span class="invalid-feedback">{{ errors[0] }}</span>
                                </ValidationProvider>
                            </div>
                        </div>

                        <div class="col-lg-3 col-xl-3">
                            <div class="form-group">
                                <ValidationProvider rules="required" v-slot="{ errors }">
                                    <label>{{$t('MeterService')}}:<span class="text-danger">*</span></label>
                                    <select v-model="Data.meterService"
                                        :class="errors[0] ? 'form-control is-invalid' : 'form-control'"
                                        name="MeterService">
                                        <option value="">Select Service</option>
                                        <option v-for="option in meterServiceOptions" :key="option.key"
                                            :value="option.value">
                                            {{ option.key }}
                                        </option>
                                    </select>
                                    <span class="invalid-feedback">{{ errors[0] }}</span>
                                </ValidationProvider>
                            </div>
                        </div>

                        <div class="col-lg-3 col-xl-3">
                            <div class="form-group">
                                <label>{{$t('MeterUnit')}}:</label>
                                <select v-model="Data.meterUnit" class="form-control">
                                    <option value="">Select Unit</option>
                                    <option v-for="option in meterUnitOptions" :key="option.key" :value="option.value">
                                        {{ option.key }}
                                    </option>
                                </select>
                            </div>
                        </div>
                    </div>

                    <div class="row p-3">
                        <div class="col-lg-12 mb-3">
                            <div class="heading-border mt-2">
                                <h5 class="h5"><span>{{$t('MeterSpecification')}}</span></h5>
                            </div>
                        </div>

                        <div class="col-lg-3 col-xl-3">
                            <div class="form-group">
                                <label>{{$t('Category')}}:</label>
                                <select v-model="Data.category" class="form-control">
                                    <option value="">Select Category</option>
                                    <option v-for="option in categoryOptions" :key="option.key" :value="option.value">
                                        {{ option.key }}
                                    </option>
                                </select>
                            </div>
                        </div>

                        <div class="col-lg-3 col-xl-3">
                            <div class="form-group">
                                <label>{{$t('Model')}}:</label>
                                <div class="d-flex">
                                    <div class="form-check form-check-inline">
                                        <input type="radio" v-model="Data.selectedOption" value="0"
                                            class="form-check-input">
                                        <label class=" ml-4">{{$t('numerical')}}</label>
                                    </div>
                                    <div class="form-check form-check-inline">
                                        <input type="radio" v-model="Data.selectedOption" value="1"
                                            class="form-check-input">
                                        <label class=" ml-4">{{$t('categorical')}}</label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="row p-3">
                        <div class="col-lg-12 mb-3">
                            <div class="heading-border mt-2">
                                <h5 class="h5"><span>{{$t('vendorInformation')}}</span></h5>
                            </div>
                        </div>

                        <div class="col-lg-3 col-xl-3">
                            <div class="form-group">
                                <label>{{$t('Vendor')}}:</label>
                                <select v-model="Data.vendor" class="form-control"
                                    @change="getvendordetails(Data.vendor)">
                                    <option value="">Select Vendor</option>
                                    <option v-for="option in vendorOptions" :key="option.key" :value="option.value">
                                        {{ option.key }}
                                    </option>
                                </select>
                            </div>
                        </div>

                        <div class="col-lg-3 col-xl-3">
                            <div class="form-group">
                                <label>{{$t('vendoremail')}}:</label>
                                <input v-model="Data.vendorEmail" type="text" disabled class="form-control">
                            </div>
                        </div>

                        <div class="col-lg-3 col-xl-3">
                            <div class="form-group">
                                <label>{{$t('vendormobile')}}:</label>
                                <input v-model="Data.vendorMobile" type="text" disabled class="form-control">
                            </div>
                        </div>
                    </div>
                    <div class="row p-3 flex-row-reverse">

                        <div class="text-end text-right col-12">
                            <button type="submit" class="btn btn-success"><i class="fa fa-save pr-2"></i>{{ $t('Save')
                                }}</button>
                            <button @click="$emit('close')" type="button" class="btn btn-danger"
                                style="margin-left: 5px;"><i class="fa fa-close pr-2"></i>{{ $t('Cancel')
                                }}</button>
                        </div>

                    </div>
                </form>
            </ValidationObserver>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate';
export default {

    components: {
        extend,
        ValidationObserver,
        ValidationProvider
    },
    data() {
        return {
            Data: {
                meterName: '',
                meterModel: '',
                meterService: '',
                meterUnit: '',
                category: '',
                selectedOption: 0,
                vendor: '',
                vendorEmail: '',
                vendorMobile: ''
            },
            meterServiceOptions: [],
            meterUnitOptions: [],
            categoryOptions: [],
            vendorOptions: []
        };
    },
    created() {
        this.GetMeterServices();
        this.GetMeterCategories();
        this.GetMeterUnits();
        this.GetMeterVendors();
    },
    methods: {
        async submit() {
            DataService.AddMeter(this.Data).then(response => {
                if (response.data == 0) {
                    this.ShowAlert(this.$t("RecordsSavedSuccessfully"), 'success', true, this.$t('Alert'));
                }
            })
        },
        async GetMeterServices() {
            DataService.GetMeterServices().then(response => {
                this.meterServiceOptions = response.data
            })
        },
        async GetMeterCategories() {
            DataService.GetMeterCategories().then(response => {
                this.categoryOptions = response.data
            })
        },
        async GetMeterUnits() {
            DataService.GetMeterUnits().then(response => {
                this.meterUnitOptions = response.data
            })
        },
        async GetMeterVendors() {
            var param = `type=${'individual'}`
            DataService.GetMeterVendors(param).then(response => {
                this.vendorOptions = response.data
            })
        },
        async getvendordetails(id) {
            var param = `userId=${id}`
            DataService.GetVendorDeatils(param).then(response => {
                this.Data.vendorEmail = response.data.email
                this.Data.vendorMobile = response.data.phone
            })
        }


    }
}
</script>